import React from 'react';
import './App.css';
import AnalyticsWrapper from '@shutterstock-private/analytics-wrapper';

import Layout from './components/Layout';

function App() {
  return (
    <div className="App">
      <Layout />
      <AnalyticsContainer />
    </div>
  );
}

class AnalyticsContainer extends React.Component {
  constructor(props) {
    super(props);

    // Create an instance of the wrapper with the ID of the Segment source to pass data to
    var wrapper = new AnalyticsWrapper({
      sourceId: 'Eb1vf7gnQNmiiKX8ESJXLenE0pG49XOk',
    }, {
    page: {
      applicationName: 'ShutterstockUIWizard',
      environment: 'production',
      pageLanguage: 'en',
      site: 'developers'
    }
  });

    // Initialize the wrapper with the initial analytics object data
    wrapper.loadProvider()
    .then(() => {
      wrapper.setInitialData(
        {
          "page": {
            "applicationName": "ShutterstockUIWizard",
            "environment": "production",
            "originalUrl": "https://tech.shutterstock.com/shutterstock-ui-wizard",
            "pageLanguage": "en",
            "pageType": "shutterstockUiWizard",
            "referringUrl": "https://tech.shutterstock.com/shutterstock-ui-wizard",
            "site": "developers"
          },
        }
      );
    });


    // Track the page load
    wrapper.page({
      pageType: 'shutterstockUiWizard',
      site: 'developers'
    });
  }

  render() {
    return <div />;
  }
}

export default App;
