import { 
  onItemClickString as onItemClickCode, 
  imageSearchFilters,
  overlayActionsString as overlayActionsCode,
} from './common-snippets';

export const getHtmlCode = ({
  mediaType,
  inputs: {
    apiKey,
    assetsPerPage = 26,
    htmlElement,
    widgetSubtitle,
    widgetTitle,
  },
  booleans: {
    dynamicSubtitle,
    dynamicTitle,
    onItemClick,
    showMore,
    showSearchBar,
    searchFilters,
    overlayActions,
  }
}) => {
  const onItemClickString = onItemClick ? onItemClickCode : '';
  const searchFiltersString = searchFilters ? imageSearchFilters : '';
  const overlayActionsString = overlayActions ? overlayActionsCode : '';

return `
<html>
  <head>
    <link rel="stylesheet" type="text/css" href="http://localhost:5000/static/css/sstk-widget.css">
  </head>
  <body>
    <div id="${htmlElement}"></div>
    <script>
      window.onload = () => {
        const widget = new ShutterstockWidget({
          container: document.getElementById('${htmlElement}'),
          dynamicTitle: ${dynamicTitle},
          dynamicSubtitle: ${dynamicSubtitle},
          key: '${apiKey}',
          mediaType: '${mediaType}',
          showMore: ${showMore},
          showSearchBar: ${showSearchBar},
          title: '${widgetTitle}',
          subtitle: '${widgetSubtitle}',
          assetsPerPage: ${assetsPerPage}${onItemClickString}${searchFiltersString}${overlayActionsString}
        });

        widget.search({
          query: ''
        });
      }
    </script>
    <script src="http://localhost:5000/static/js/sstk-widget.js"></script>
  </body>
</html>
`
};
