import { useEffect, useRef, useCallback } from 'react';
import insertPreviewSvg from '../../images/insert-preview.svg';

const useSearchWidget = ({
  apiKey,
  assetsPerPage = 26,
  dynamicSubtitle,
  dynamicTitle,
  htmlElement,
  mediaType,
  onItemClick,
  showItemId,
  showMore,
  showSearchBar,
  widgetSubtitle,
  widgetTitle,
  searchFilters,
  overlayActions,
}) => {
  const widgetInstanceRef = useRef();
  const widgetRef = useRef();

  const configureWidget = useCallback(() => {
    const widgetConfig = {
      mediaType,
      title: widgetTitle,
      subtitle: widgetSubtitle,
      container: widgetRef.current,
      showMore,
      key: apiKey,
      showItemId,
      dynamicTitle,
      dynamicSubtitle,
      showSearchBar,
      assetsPerPage,
      ...(onItemClick && {
        onItemClick: (e, item) => {
          e.preventDefault();
          console.log(item);
        }
      }),
      ...(searchFilters && {
        searchFilters: {
          showFilterDrawer: true,
          images: {
            imageTypeFilter: true,
            orientationFilter: true
          }
        }
      }),
      ...(overlayActions && {
        overlayActions: [
          {
            label: 'Insert preview',
            icon: insertPreviewSvg,
            onClick: (e, item) => {
              e.preventDefault();
              alert(`Inserting preview image:${item.id}`);
            }
          },          
        ],
      }),
    };

    // eslint-disable-next-line no-undef
    if (typeof window === 'object' && window.ShutterstockWidget) {
      // eslint-disable-next-line no-undef
      const widgetInstance = new window.ShutterstockWidget(widgetConfig);
      widgetInstance.search({
        query: '',
      });
      widgetInstanceRef.current = widgetInstance;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    apiKey,
    assetsPerPage,
    dynamicSubtitle,
    dynamicTitle,
    htmlElement,
    mediaType,
    onItemClick,
    showItemId,
    showMore,
    showSearchBar,
    widgetSubtitle,
    widgetTitle,
    searchFilters,
    overlayActions,
  ]);

  useEffect(configureWidget, [
    apiKey,
    assetsPerPage,
    dynamicSubtitle,
    dynamicTitle,
    htmlElement,
    mediaType,
    onItemClick,
    showItemId,
    showMore,
    showSearchBar,
    widgetSubtitle,
    widgetTitle,
    searchFilters,
    overlayActions,
  ]);

  return {
    widgetInstanceRef,
    widgetRef,
  };
};

export default useSearchWidget;
